.friendList {
    list-style: none;
    margin-top: 50px;
    padding: 0;
  
  }
  
  .item {
    display: flex;
    border: 1px solid black;
    width: 300px;
    margin-bottom: 5px;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);    
  }
  .avatar {
    padding: 10px;
    margin-right: 30px;
  }
  .name {
    text-align: center;
    font-size: 18px;
  }
  
  .status {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-top: 30px;
    margin-left: 2px;
  }
  
  .true {
    composes: status;
    background-color: green;
  }
  .false {
    composes: status;
    background-color: red;
  }